/* O Animation */

.O-stroke {
  stroke-dasharray: 302px;
  stroke-dashoffset: 302px;
  animation: O-move 20s ease forwards;
}

@keyframes O-move {
  0% {
    stroke-dashoffset: 302px;
  }
  4% {
    stroke-dashoffset: 302px;
  }
  9% {
    stroke-dashoffset: 0px;
  }
  100% {
    stroke-dashoffset: 0px;
  }
}

/* Red Dot Animation */

.red-dot {
  stroke-width: 44px;
  stroke-linecap: round;
  animation: red-dot-grow 8s ease-out forwards;
}

@keyframes red-dot-grow {
  0% {
    stroke-width: 0px;
  }
  15% {
    stroke-width: 0px;
  }
  20% {
    stroke-width: 44px;
  }
  100% {
    stroke-width: 44px;
  }
}

@media only screen and (max-width: 600px) {
  .hello-parent {
    position: relative;
    top: 10px;
    height: 40%;
    width: 60%;
    margin: 15px;
  }

  .hello-parent {
    transform: scale(0.7);
  }
}

/* Galaxy fold */
@media only screen and (max-width: 280px) {
  .hello-parent {
    position: relative;
    top: 10px;
    height: 40%;
    width: 60%;
    margin: 15px;
  }

  .hello-parent {
    transform: scale(0.4);
  }
}

/* Nest Hub */
@media only screen and (width: 1024px) {
  .hello-parent {
    position: relative;
    top: 10px;
    height: 40%;
    width: 60%;
    margin: 15px;
  }

  .hello-parent {
    transform: scale(0.6);
  }
}
