@media only screen and (min-width: 600px) {
  .menu {
    text-transform: uppercase;
    color: #474747e4;
    /* display: inline-block; */
    cursor: pointer;
    pointer-events: none;
    height: 270px;
    width: 99.4%;
    font-family: "Silkscreen";
    font-size: 1.5rem;
    background: #ffffff;
    background-image: url("https://www.transparenttextures.com/patterns/concrete-wall.png");
    border-top-left-radius: 25% 25%;
    border-top-right-radius: 25% 25%;
    border: 4px solid #00000011;
  }
}
.menu:hover {
  pointer-events: all;
}
.label {
  display: flex;
  align-content: center;
  justify-content: center;
  /* position: relative;
  width: 180px; */
  margin-top: 20px;
  /* left: 43%; */
  cursor: pointer;
  font-size: 1.2em;
  pointer-events: all;
}

.item-container {
  display: flex;
  align-content: center;
  justify-content: space-evenly;
  margin-top: 30px;
  justify-content: center;
  align-items: center;
}
.item-container div span {
  text-align: center;
}

.spacer:before {
  content: "";
  position: absolute;
  border-bottom: 1px solid #ffffff;
  height: 1px;
  width: 0%;
  transition: width 0.25s ease;
  transition-delay: 0.7s;
}
.item {
  display: flex;
  align-content: center;
  justify-content: space-evenly;
  top: 10px;
  margin-left: 10px;
  margin-right: 10px;
  opacity: 0;
  transition: opacity 0.5s ease, top 0.5s ease;
  transition-delay: 0;
}
span {
  transition: color 0.5s ease;
}
.item:hover span {
  color: #ff0000;
}
.menu:hover .spacer:before {
  width: 100%;
  transition-delay: 0s;
}
.menu:hover .item {
  opacity: 1;
  top: 0px;
}
.item:nth-child(1) {
  transition-delay: 0.45s;
}
.item:nth-child(2) {
  transition-delay: 0.4s;
}
.item:nth-child(3) {
  transition-delay: 0.35s;
}
.item:nth-child(4) {
  transition-delay: 0.3s;
}
.item:nth-child(5) {
  transition-delay: 0.25s;
}
.item:nth-child(6) {
  transition-delay: 0.2s;
}
.item:nth-child(7) {
  transition-delay: 0.15s;
}
.item:nth-child(8) {
  transition-delay: 0.1s;
}
.item:nth-child(9) {
  transition-delay: 0.05s;
}
.item:nth-child(10) {
  transition-delay: 0s;
}
.menu:hover .item:nth-child(1) {
  transition-delay: 0.25s;
}
.menu:hover .item:nth-child(2) {
  transition-delay: 0.3s;
}
.menu:hover .item:nth-child(3) {
  transition-delay: 0.35s;
}
.menu:hover .item:nth-child(4) {
  transition-delay: 0.4s;
}
.menu:hover .item:nth-child(5) {
  transition-delay: 0.45s;
}
.menu:hover .item:nth-child(6) {
  transition-delay: 0.5s;
}
.menu:hover .item:nth-child(7) {
  transition-delay: 0.55s;
}
.menu:hover .item:nth-child(8) {
  transition-delay: 0.6s;
}
.menu:hover .item:nth-child(9) {
  transition-delay: 0.65s;
}
.menu:hover .item:nth-child(10) {
  transition-delay: 0.7s;
}

.footer-end {
  display: flex;
  align-items: center;
  justify-items: center;
  flex-direction: column;
  margin-top: 20px;
  margin-bottom: 20px;
}

