@media only screen and (min-width: 600px) {
  .header {
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    box-shadow: none;
    background-color: #ffffff00;
    height: 60px;
    overflow: hidden;
    z-index: 10;
  }

  .main {
    margin: 0 auto;
    display: block;
    height: 100%;
    margin-top: 60px;
  }
  .mainInner {
    display: table;
    height: 100%;
    width: 100%;
    text-align: center;
    size: 150%;
  }
  .mainInner div {
    display: table-cell;
    vertical-align: middle;
    font-size: 3em;
    font-weight: bold;
    letter-spacing: 1.25px;
    size: 150%;
  }
  #sidebarMenu {
    height: 100%;
    position: fixed;
    width: 7vw;
    margin-top: 60px;
    transform: translateX(-250px);
    transition: transform 250ms ease-in-out;
    background: rgba(255, 192, 203, 0);
  }
  .sidebarMenuInner {
    position: relative;
    right: 10px;
    margin: 0;
    padding: 0;
    /* border-top: 1px solid rgba(255, 255, 255, 0.1); */
  }

  .sidebarMenuInner li img {
    transform: scale(0.6);
    cursor: pointer;
  }
  .sidebarMenuInner li img:hover {
    transform: scale(0.7);
    cursor: pointer;
  }

  input[type="checkbox"]:checked ~ #sidebarMenu {
    transform: translateX(0);
  }

  input[type="checkbox"] {
    transition: all 0.3s;
    box-sizing: border-box;
    display: none;
  }
  .sidebarIconToggle {
    transition: all 0.3s;
    box-sizing: border-box;
    cursor: pointer;
    position: fixed;
    z-index: 99;
    height: 100%;
    width: 100%;
    top: 4vh;
    left: 46px;
    height: 22px;
    width: 22px;
    transform: scale(2.5);
  }
  .spinner {
    transition: all 0.3s;
    box-sizing: border-box;
    position: absolute;
    height: 3px;
    width: 100%;
    background-color: #2f2f2f;
    border: 1px solid white;
  }
  .horizontal {
    transition: all 0.3s;
    box-sizing: border-box;
    position: relative;
    float: left;
    margin-top: 2.5px;
    transform: scale(1.01);
  }
  .diagonal.part-1 {
    position: relative;
    transition: all 0.3s;
    box-sizing: border-box;
    float: left;
  }
  .diagonal.part-2 {
    transition: all 0.3s;
    box-sizing: border-box;
    position: relative;
    float: left;
    margin-top: 3px;
  }
  input[type="checkbox"]:checked ~ .sidebarIconToggle > .horizontal {
    transition: all 0.3s;
    box-sizing: border-box;
    opacity: 0;
  }
  input[type="checkbox"]:checked ~ .sidebarIconToggle > .diagonal.part-1 {
    transition: all 0.3s;
    box-sizing: border-box;
    transform: rotate(135deg);
    margin-top: 8px;
  }
  input[type="checkbox"]:checked ~ .sidebarIconToggle > .diagonal.part-2 {
    transition: all 0.3s;
    box-sizing: border-box;
    transform: rotate(-135deg);
    margin-top: -9px;
  }

  .home {
    position: absolute;
    font-family: "Silkscreen";
    left: 40px;
    top: 10px;
    text-shadow: 2px 2px 2px #ffffff;
  }

  .tch {
    width: 100px;
    position: absolute;
    text-align: center;
    word-wrap: none;
    font-family: "Silkscreen";
    left: 12px;
    top: 120px;
    text-shadow: 2px 2px 2px #ffffff;
  }

  .projects {
    position: absolute;
    font-family: "Silkscreen";
    left: 20px;
    top: 270px;
    text-shadow: 2px 2px 2px #ffffff;
  }

  .connect {
    position: absolute;
    font-family: "Silkscreen";
    left: 21px;
    top: 400px;
    text-shadow: 2px 2px 2px #ffffff;
  }
}

/* Cell Phones */

@media only screen and (max-width: 600px) {
  .header {
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    box-shadow: none;
    background-color: #ffffff00;
    height: 60px;
    overflow: hidden;
    z-index: 10;
  }

  .sidebarMenuInner {
    display: none;
    /* border-top: 1px solid rgba(255, 255, 255, 0.1); */
  }
  .sidebarMenuInner li {
    display: inline;
  }

  .sidebarMenuInner li img {
    transform: scale(0.6);
  }

  input[type="checkbox"]:checked ~ #sidebarMenu {
    display: none;
  }

  input[type="checkbox"] {
    transition: all 0.3s;
    box-sizing: border-box;
    display: none;
  }
  .sidebarIconToggle {
    display: none;
  }

  .home {
    position: absolute;
    font-family: "Silkscreen";
    left: 40px;
    top: 10px;
    text-shadow: 2px 2px 2px #ffffff;
  }

  .tch {
    width: 120px;
    position: absolute;
    text-align: center;
    /* word-wrap: none; */
    font-family: "Silkscreen";
    left: 130px;
    top: 10px;
    text-shadow: 2px 2px 2px #ffffff;
  }

  .projects {
    position: absolute;
    font-family: "Silkscreen";
    left: 1px;
    top: 10px;
    text-shadow: 2px 2px 2px #ffffff;
  }

  .connect {
    position: absolute;
    font-family: "Silkscreen";
    left: 21px;
    top: 400px;
    text-shadow: 2px 2px 2px #ffffff;
  }
}

/* Galaxy fold */
@media only screen and (max-width: 280px) {
  .header {
    display: none;
  }
}
